import React, { Component } from 'react'
import '../css/TabsNav.css'
import  TabAddVideos from './tabs_nav/TabAddVideos'
import TabSingleMedia from './TabSingleMedia.js'
import Alert from "sweetalert2";
import {getAllMedia,uploadFile} from  './Utils/Utils.js'



export default class TabContentVideos extends Component {

  constructor(props){
    super(props)

    getAllMedia().then(data=>{

      console.log(data.data)


    })

  }




  onChangeHandler=event=>{


    console.log(event.target.files[0])
    this.setState({
        selectedFile: event.target.files[0],
        loaded: 0,
      },()=>{ 
        console.log(this.state)
      })

}

  onClickHandler = (e) => {

    e.preventDefault()  

    if(this.state.selectedFile === null){

      Alert.fire({
        icon: 'error',
        title: 'Oops...',
        text: 'Seems like you missed a few fields !'
      })

    }


    else{

  
      const data = new FormData() 
      data.append('file', this.state.selectedFile)

      Alert.fire({
        icon: 'infor',
        title: 'File Uploading',
        text: 'Please wait',
        showLoader: true
      })


      uploadFile(data).then(data=>{

        console.log("DATA",data)

        if(data.status===200){

          window.location.reload()

        }

        else{
          Alert.fire({
            icon: 'error',
            title: 'Oops...',
            text: 'Something went wrong check your internet connection!'
          })

      }


    })
    
    }

  } 


  render() {
    return (
      <div>
        <div className="container">
          <TabAddVideos/>
          <hr/>
          <TabSingleMedia/>          


        
          <button type="button" className="btn btn-primary addNewDeviceButton" data-toggle="modal" data-target="#add_new_device">
                + Add new video
          </button>


          <div className="modal fade" id="add_new_device" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
          <div className="modal-dialog" role="document">
            <div className="modal-content">
            <div className="modal-header">
                <h4 className="modal-title newDeviceModal" id="exampleModalLabel">New device</h4>
                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                <span aria-hidden="true">&times;</span>
                </button>
            </div>
            <div className="modal-body">
            <form>
              <input type="file" name="file" onChange={this.onChangeHandler}/>
              <hr/>
              <div className="modal-footer">
                  <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                  <button type="button" className="btn btn-primary"  onClick={this.onClickHandler}>Upload</button>
              </div>
            </form>
            </div>
          </div></div></div>
          </div>
      </div>
    )
  }
}
